import React from 'react'
import Footer from '../Footer'
import '../css/Menu.css'
import Menu from './Menu'
import menus from './MenusData'
import meals from './MealsData'
import Roulette from '../Roulette'
import Category from './Category'
import Dish from './Dish'


const Menus = () => {
  const language = 'es'
  const name = 'Asia Oriental Taste'.toUpperCase().split('')
  const restaurantsTitle = {
    es: 'Prueba todos nuestros platos y menús ahora!',
    cat: '',
    it: 'Prova subito tutti i nostri piatti e menù!'
  }
  const menusTitle = {
    es: 'Nuestros Menus',
    cat: '',
    it: 'I nostri menù'
  }
  const { day, night, festive, pedidos } = menus

  const allYouCanEat = {
    es: 'Todo lo que puedas comer',
    cat: '',
    it: ''
  }
  const allYouCanEatDescription = {
    es: 'Elige lo que quieras, saborea y disfruta todos nuestros deliciosos platos sin limites',
    cat: '',
    it: 'Scegli quello che vuoi, senza limiti e assapora tutti i nostri piatti'
  }
  const takeAway = {
    es: 'Lleva contigo',
    cat: '',
    it: '',
    en: 'Take Away'
  }
  const takeAwayDescription = {
    es: 'Escoge entre los numerosas platos que tenemos disponibles para ti para poder ser degustados y disfrutados en el lugar que quieras',
    cat: '',
    it: ''
  }

  const mealsTitle = {
    es: 'Algunos de Nuestros Platos',
    cat: '',
    it: ''
  }
  const dulcesTitle = {
    es: 'Nuestros Dulces',
    cat: '',
    it: ''
  }
  const vinosTitle = {
    es: 'Nuestros Vinos',
    cat: '',
    it: ''
  }
  const dulcesButton = {
    es: 'Carta Dulces',
    cat: '',
    it: ''
  }
  const vinosButton = {
    es: 'Carta Vinos',
    cat: '',
    it: ''
  }



  return (
    <div className='main-menu'>
      <div className='bg-wrapper'>
        <div className='title-menu'>
          {name.map((letter, index) => <div key={index} className='letter' style={{ transform: `translate(-50%,-50%) rotate(${index * (160 / (name.length - 1)) - 80}deg)` }}>{letter}</div>)}
          <div className='logo' />
        </div>
        <div className='restaurant-title'>{restaurantsTitle[language]}</div>
        <div className='menu-top'>
          <div className='line' />
          <div className='icon' />
          <div className='line' />
        </div>
      </div>
      <div className='menus-container'>
        <div className='menus-section'>{menusTitle[language]}</div>
        <div className='wrapper'>
          <Menu menu={day} />
          <Menu menu={night} />
          <Menu menu={festive} />
        </div>
      </div>

      <div className='allYouCan'>
        <div className='container'>
          <div className='left'>
            <div className='name font1'>Asia Oriental Taste</div>
            <div className='title'>{allYouCanEat[language]}</div>
            <div className='description font4'>{allYouCanEatDescription[language]}</div>
          </div>
          <div className='right' >
            <Roulette />
          </div>
        </div>
      </div>

      <div className='takeaway'>
        <div className='main-title'>{takeAway[language]}</div>
        <img alt='restaurant' />
        <div className='promo'>{takeAwayDescription[language]}</div>
        <div className='wrapper'>
          <Menu menu={pedidos} />
        </div>
      </div>

      <div className='meals'>
        <div className='fan' />
        <div className='main-title'>{mealsTitle[language]}</div>
        {meals.map((category =>
          <Category key={category.name[language]} name={category.name[language]}>
            {category.meals.map(meal => <Dish key={meal.name} dish={meal} />)}
          </Category>))}
        <div className='wrapper'>
          <a href='./pdfs/CartaNoche.pdf' target='blank'>
            <div className='menus'>
              <div className='name'>Todo</div>
              <div className='arrow' />
            </div>
          </a>
        </div>
        <div className='fan2' />
      </div>

      <div className='cartas'>
        <div className='menu-top'>
          <div className='line' />
          <div className='name font1'>Asia Oriental Taste</div>
          <div className='line' />
        </div>
        <div className='wrapper'>
          <div className='carta'>
            <div className='carta-bg'>
              <div className='carta-title'>{dulcesTitle[language]}</div>
              <div className='carta-button'>
                <a href='./pdfs/CartaDulces.pdf' target='blank' >
                  {dulcesButton[language]}
                </a>
              </div>
              <div className='carta-image carta-dulces' />
            </div>
          </div>
          <div className='carta'>
            <div className='carta-bg'>
              <div className='carta-title'>{vinosTitle[language]}</div>
              <div className='carta-button'>
                <a href='./pdfs/CartaVinos.pdf' target='blank' >
                  {vinosButton[language]}
                </a></div>
              <div className='carta-image carta-vinos' />
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  )
}

export default Menus